html,
body,
#root {
  height: 100%;
  width: 100%;
}

.App {
  width: 100%;
  height: 100%;
  background: lightsalmon;
  border: 1px colid red;
}

.Fullscreen {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.Frame {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.Frame_text {
  font-weight: 700;
  font-size: 45px;
  color: grey;
}
